import * as React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { Tilt } from 'react-next-tilt';
import './index.css';




const BlogIndex = ({ data, location }) => {

  const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  const siteTitle = data.site.siteMetadata?.title || `Title`
  const posts = data.allMarkdownRemark.nodes

  const [url, setUrl] = React.useState('')
  const [jump_url, setJumpUrl] = React.useState('')
  // const posts = data.allMarkdownRemark.nodes
  const styleImg = {
    width: '400px',
    height: '560px',
  }
  console.log(posts)
  console.log("随机一卡")
  const randomIndex = getRandomInt(0, posts.length - 1);
  console.log(randomIndex)
  setUrl(`/cards/${posts[randomIndex].frontmatter.id}.png`)
  setJumpUrl(`/${posts[randomIndex].frontmatter.id}`)

  return (
    <Layout location={location} title={siteTitle} >
      <div>
        <h2>随机一卡</h2>
        {url}
        <a key={jump_url} href={jump_url}>
          <Tilt key={url} style={styleImg} scale={1.0} shadowEnable={true} lineGlareEnable={false} spotGlarePosition="all" borderRadius="18px">
            <img key={url} src={url} style={styleImg} />
          </Tilt>
        </a>
      </div>


    </Layout>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="All posts" />

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: { frontmatter: { type: { eq: "card" } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          id
        }
      }
    }
  }
`
